@import '@styles/shared.scss';

.contact-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 33px;

  &__item:first-child {
    color: $accent;
  }

  &__link {
    display: block;
    padding: 16px 0;
    font-weight: 400;
    line-height: 1.33;
    &:hover {
      text-decoration: underline;
    }
  }
}
